/**
 * like check for data
 *
 * @param {*object} searchCondition: { name, value }
 * @param {*boolean} caseSensitive: false
 * @param {*DataObject} data
 */
var like = function (searchCondition, caseSensitive, data) {
    var key = searchCondition.key, value = searchCondition.value;
    var targetValue = data[key];
    // disable like search if targetValue is array
    if (Array.isArray(targetValue)) {
        return false;
    }
    if (caseSensitive) {
        return targetValue.toString().includes(value.toString());
    }
    return targetValue
        .toString()
        .toUpperCase()
        .includes(value.toString().toUpperCase());
};
export default like;
