import { curry } from '../lib/utils';
/**
 * is null check
 *
 * @param {*boolean} includeNull: false
 * @param {*object} searchCondition: { name, value }
 * @param {*Predicator} predicator: normal predicator
 * @param {*DataObject} data
 */
var targetValueIsNull = function (includeNull, searchCondition, predicator, data) {
    var key = searchCondition.key;
    var targetValue = data[key];
    // value is null or undefined
    if (targetValue == null) {
        return includeNull;
    }
    return predicator(data);
};
export default curry(targetValueIsNull);
